*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body
{
    background-color: blanchedalmond;
    height: 100vh;
    width: 100%;
}
.app
{
    margin-top: 10vh;
    padding:10px;text-align: center;
}
.app input
{
    width: 250px;
    padding: 10px;
    font-size: 16px;
    font-weight: 600;
    color:navy;
    border-radius: 10px;
    border: 2px solid rgb(78, 179, 78);
    background: transparent;
}
.app button
{
    width: 100px;
    height: 30px;
    padding: 5px;
    margin-top: 10px;
    background-color:rgb(15, 82, 15);
    border: 0;
    color:white;
    font-size: 16px;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    cursor: pointer;
}
.app input:focus
{
    border-color:navy;
    outline: 0;
    background-color:rgb(198, 243, 198);
}
.app h1
{
    color: green;
    margin-bottom: 10px;
}
.navbar
{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    height:60px;
    width: 100%;
    background-color:blue;
    color: white;
}
nav ul
{
    display: flex;
    align-items: center;
    justify-content:space-between;
    list-style: none;
    width: 60%;
    font-size: 20px;
}
nav ul li
{
    transition: all 0.4s ease-in-out;
    cursor: pointer;
}
nav ul li:hover
{
    transform: scale(1.2);
}
.navActive
{
    color:greenyellow;
    font-size: 25px;
    font-weight: bold;
}
.app-box
{
    display: flex;
    justify-content: center;margin-top: 50px;
}
.app-box .one
{
width:auto;
height:auto;
padding: 10px 25px;
background-color: #FF3CAC;
background-image: -webkit-linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
background-image: -moz-linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
background-image: -o-linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
color:yellow;display: grid;place-items: center;
border-radius: 50px;
}
.app-box .one
{
    font-size:20px;
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-weight: 500;
}
.app-box .para
{
    font-size: 20px;
    color: green;

}